import styled from 'styled-components'
import {Link as LinkScroll} from 'react-scroll'
import {Link as LinkRoute} from 'react-router-dom'

export const Nav = styled.nav`
  background:${({scrolling})=>scrolling? "#2C3E50":"transparent" };
  height:80px;
  margin-top:-80px;
  display:flex;
  justify-content: center;
  align-items:center;
  font-size:1rem;
  position:sticky;
  top:0;
  z-index:10;
 

  @media screen and (max-width:768px){
      transition:0.8s all ease ;
  }

`;

export const NavbarContainer = styled.div`


  height:80px;
  display:flex;
  justify-content: space-between;
  width:100%;
  z-index:1;
  padding: 0 ,24px;
  max-width: 1100px;
  
`;



export const MobileIcon=styled.div`

display:none;

@media screen and (max-width:768px){
    display:block;
    position:absolute;
    top:0;
    right: 0;
    transform:translate(-100%,60%);
    font-size:1.8rem;
    cursor: pointer;
    color:#fff;
}

`;

export const NavMenu=styled.ul`

display:flex;
align-items:center;
list-style:none;
text-align:center;
margin-right:-22px;
@media screen and (max-width:768px){

    display:none;
}
`;

export const NavItem=styled.li`
height:80px;
`;


export const NavLinks=styled(LinkScroll)`

color:#fff;
display:flex;
align-items:center;
text-decoration:none;
padding: 0 1rem;
height:100%;
cursor: pointer;

&.active{
    
    border-bottom: 3px solid #FFD700;
}
`;

export const NavBtn =styled.nav`

display: flex;
margin-left:2rem;
align-items:center;
`;

export const NavBtnLink = styled(LinkRoute)`
border-radius:50px;
background:#fff;
white-space: nowrap;
padding: 10px 22px;
color: #010606; 
font-size:16px;
outline:none; 
border:none;
cursor:pointer;
transition:all 0.2s ease-in-out;
text-decoration:none;

&:hover{    
    transition:all 0.2s ease-in-out;
    background:#FFD700;
    color:#010606; 
} 
 
`;